.search-section {
  padding-bottom: 20px;
  position: relative;
}

.search-form-container {
  z-index: 100;
  width: 100%;
  background-color: #23449f;
  padding: 10px 20px;
  position: fixed;
  box-shadow: 0 4px 2px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.search-form {
  max-width: 420px;
  width: 100%;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  transition: outline .25s linear;
  display: flex;
  overflow: hidden;
}

.search-form:focus-within {
  outline: 3px solid #2196f3;
}

.search-form input {
  height: 40px;
  width: 100%;
  border: none;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  outline: none;
  padding: 10px;
  display: block;
  box-shadow: inset 0 0 5px 2px rgba(0, 0, 0, .3);
}

.search-form button {
  min-width: 40px;
  color: #302d2d;
  cursor: pointer;
  border: none;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  outline: none;
  justify-content: center;
  align-items: center;
  transition: color .25s linear, background-color .25s linear;
  display: flex;
}

.search-form button:hover, .search-form button:focus {
  color: #fff;
  background-color: #2196f3;
}

.icon-btn {
  fill: currentColor;
}

.spinner {
  width: 60px;
  height: 60px;
  border: 8px solid #5e96ce;
  border-top-color: #c8cbd1;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  animation: spin .75s linear infinite;
  display: block;
}

.spinner.is-hidden {
  display: none;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.gallery {
  grid-template-columns: repeat(1, 1fr);
  gap: 20px;
  padding: 80px 15px 20px;
  display: grid;
}

.photo-card {
  color: #000;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  outline: none;
  text-decoration: none;
  transition: transform .25s linear, box-shadow .25s linear;
  display: block;
  transform: scale(1);
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.photo-card:hover, .photo-card:focus {
  transform: scale(1.05);
  box-shadow: 0 5px 8px rgba(0, 0, 0, .2), 0 3px 2px rgba(0, 0, 0, .08), 0 3px 2px rgba(0, 0, 0, .12);
}

.photo-card-thumb {
  width: 100%;
  height: 200px;
}

.photo-card-thumb > .photo-card-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.info {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 15px;
  display: flex;
}

.info > .info-item {
  text-align: center;
  flex-direction: column;
  gap: 8px;
  margin: 0;
  display: flex;
}

